import React from 'react';
import MenuAppBar from './Header';
import {useStylesLight, useStylesDark} from './css/WaitingRoomStyle'
import {Avatar, Box, Button, Card, CardActions, CardContent, Checkbox, CssBaseline, Divider, FormControlLabel, Grid, Link, Paper, TextField, Typography, Slider} from '@material-ui/core';
import {Column, Row, Item} from '@mui-treasury/components/flex';
import {BiSend} from 'react-icons/bi';
import {SetConnecte, SetJeu, GetTab, SetTab} from './index';
import {Connexion, MyId, ConnectionToHost} from './connexion';
import {getTheme, setTheme} from './theme';
import cx from 'clsx';

var light;
var dark;
var classes;
var styles;

export function ChangeThemeWaiting(){
  var theme = getTheme();

  if(theme == "light") {
    classes = light;
    styles = light;
  }
  else {
    classes = dark;
    styles = dark;
  }
}

export function valueManche(value) {
  var tab = GetTab();
  tab[0].manche = value;
  SetTab(tab);
  return value;
}

export function valueTemps(value) {
  var tab = GetTab();
  tab[0].temps = value;
  SetTab(tab);
  return value;
}

export default function SignInSide() {
  light = useStylesLight();
  dark = useStylesDark();
  ChangeThemeWaiting();
  var etatjeu = "sign";

  //genration de mon ID
  const getPeerId = () => {
     var myId = MyId();
     document.getElementById("zoneId").innerHTML = "";
     document.getElementById("textAndButton").innerHTML = myId;
     SetConnecte(true);
  };

  //lance le jeu
  const start = () => {
      SetJeu();
  }

  //connexion √† l'hote
  const addpool = () => {
    var id = document.getElementById("peerID").value;
    ConnectionToHost(id);
    document.getElementById("zoneId").innerHTML = "";
    document.getElementById('textAndButton').innerHTML="Connect√©!";
  }

  return (
    <Grid container xs={12} component="main" className={classes.root}>
      <CssBaseline />
      <Grid item xs={true} sm={12} >
        <Row><MenuAppBar /></Row>
      </Grid>
      <Grid item xs={12} sm={4} component={Paper} elevation={6} square>
        <div className={classes.paper}>
         <Column p={0} gap={0} className={styles.cardjoueur}>
           <Row wrap p={2} alignItems={'baseline'} className={styles.header}>
             <Item stretched className={styles.headline}>Participant.e.s</Item>
             <Item className={styles.actions}>
               Score
             </Item>
           </Row>
           <div id="playerZone"></div>
         </Column>
         <Column p={0} gap={0} container className={classes.cardco}>
         <div id = 'zoneId' className={classes.IDstyle}>
           <Row className={classes.copyright}><i>G√©n√©rez une ID pour cr√©er une session et transmettez-l√† √† vos ami.e.s pour qu'ils et elles s'y connectent!</i></Row>
           <Button
             type="submit"
             fullWidth
             variant="contained"
             color="primary"
             onClick={getPeerId}
           >G√©n√©rer une ID</Button>
         </div>
         <div id='textAndButton' className={classes.IDstyle}>
           <TextField
             variant="outlined"
             margin="normal"
             required
             fullWidth
             id="peerID"
             label="Host_Id"
             name="HostId"
             autoComplete="Host_Id"
             autoFocus
           />
           <Button
             type="submit"
             fullWidth
             variant="contained"
             color="primary"
             onClick={addpool}
           >Connexion</Button>
          </div>
          </Column>
        </div>
      </Grid>
      <Grid item xs={12} sm={3} elevation={6} square className={classes.cardchat}>
          <div id="chatBox" className="card-body"></div>
          <div className="card-footer input-group">
              <input id="message" className="form-control type_msg" placeholder="Type your message..."></input>
              <div className="input-group-append">
                  <button id="send" className="input-group-text send_btn" ><BiSend/></button>
              </div>
          </div>
      </Grid>
     <Grid item xs={false} sm={4} className={classes.paper}>
      <Column p={0} gap={0} container direction="row" className={classes.card}>
        <Row wrap p={2} alignItems={'baseline'} className={classes.header}>
          <Item stretched className={classes.headline}>Choisis une phase de jeu</Item>
        </Row>
        <Typography className ={styles.slide} id="discrete-slider" gutterBottom>
          Temps
        </Typography>
        <Slider
          className ={styles.slide}
          defaultValue={60}
          getAriaValueText={valueTemps}
          aria-labelledby="discrete-slider"
          valueLabelDisplay="auto"
          step={10}
          marks
          min={10}
          max={120}
        />
        <Typography className ={styles.slide} id="discrete-slider" gutterBottom>
          Manche
        </Typography>
        <Slider
          className ={styles.slide}
          defaultValue={4}
          getAriaValueText={valueManche}
          aria-labelledby="discrete-slider"
          valueLabelDisplay="auto"
          step={1}
          marks
          min={1}
          max={10}
        />
        <Button className ={styles.button}
          type="submit"
          fullWidth
          variant="contained"
          onClick={start}
          >Jouer</Button>
        </Column>
    </Grid>
  </Grid>
  );
}
